<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <!--呢个系图片来的，直接干掉-->

    <leftNav></leftNav>

    <div class="main">
      <router-view></router-view> <!--加一个div包住距，等于平级，唔知点解呢-->
    </div>
  </div>
</template>

<script>

import leftNav from '@/components/commnn/leftNav' //引入leftNav组件

// 之前咱们在定义组件的时候，会有一个配置对象，这里写的就是那个配置对象
	// 因为现在使用了vue提供的一套开发工具，有了vue文件这个概念，
	// 每个文件中都有模版、脚本、样式，他们是一体的，所以这里就不用再指定template选项了。
export default {
  name: 'app',
  components: {
    leftNav   //引入后在vue的构造器里添加components属性，并放入我们的leftNav组件。
  }
}
</script>

<style>
#app {
  font-family: 'Mircosoft YaHei''Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}

/* .icon-gongnengjianyi {
  font-size: 100px;
} */


.main{
  float: left;
  width: 95%;
  background:#eff2f7;
  height: 100%;
  overflow: hidden;
}

</style>



