<template>
    <div class="pos">
        <!-- hello 灰灰 -->

        <!-- element-ui系el开头的 -->
        <el-row>
            <el-col :span='7' class="order" id="orders">
                <!--跟bootstrap很像=== Element支持用24栏的形式进行布局。-->

                <el-tabs>
                    <el-tab-pane label="点餐">

                        <el-table :data="tableData" border style="width: 100%">
                            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                            <el-table-column prop="count" label="数量" width="70"></el-table-column>
                            <el-table-column prop="price" label="金额" width="70"></el-table-column>
                            <el-table-column label="操作" width="100" fixed="right">
                                <template scope="scope">
                                    <el-button type="text" size="small" @click="delOrderList(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>

                        <div class="TabsDiv">
                            <small>数量：</small>{{totalCount}}个
                            <small>金额：</small>{{totalMoney}}元
                        </div>

                    </el-tab-pane>
                    <div class="block">
                        <!-- <span class="demonstration">默认显示颜色</span> -->
                        <span class="wrapper">
                            <!-- <el-button type="success">挂单</el-button> -->
                            <el-button type="danger" @click="delAllGoods()">全部删除</el-button>
                            <el-button type="warning" @click="checkout()">结账</el-button>
                            <!-- <el-button type="info">信息按钮</el-button> -->
                        </span>
                    </div>
                    <el-tab-pane label="挂单">
                        <!-- 挂单 -->
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        <!-- 外卖 -->
                    </el-tab-pane>
                </el-tabs>

            </el-col>

            <el-col :span='17'>
                <div class="often-goods">
                    <div class="title">常用商品</div>
                    <div class="often-goods-list">
                        <ul>
                            <!-- <li v-for="goods in oftenGoods"> -->
                            <li v-for="(goodss,index) in oftenGoods" :key="index" @click="addOrderList(goodss)">
                                <span>{{goodss.goodsName}}</span>
                                <span class="money">{{goodss.price}}</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="goods-type">
                    <el-tabs>
                        <el-tab-pane label="汉堡">

                            <ul class="cookList">
                                <li v-for="(food,index) in type0Goods" :key="index" @click="addOrderList(food)">
                                    <!--遍历循环-->
                                    <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                    <span class="foodImg">{{food.goodsName}}</span>
                                    <span class="foodImg">￥{{food.price}}元</span>
                                </li>
                            </ul>

                        </el-tab-pane>
                        <el-tab-pane label="小食">
                            <ul class="cookList">
                                <li v-for="(food,index) in type1Goods" :key="index" @click="addOrderList(food)">
                                    <!--遍历循环-->
                                    <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                    <span class="foodImg">{{food.goodsName}}</span>
                                    <span class="foodImg">￥{{food.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <ul class="cookList">
                                <li v-for="(food,index) in type2Goods" :key="index" @click="addOrderList(food)">
                                    <!--遍历循环-->
                                    <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                    <span class="foodImg">{{food.goodsName}}</span>
                                    <span class="foodImg">￥{{food.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <ul class="cookList">
                                <li v-for="(food,index) in type3Goods" :key="index" @click="addOrderList(food)">
                                    <!--遍历循环-->
                                    <span class="foodImg"><img :src="food.goodsImg" width="100%"></span>
                                    <span class="foodImg">{{food.goodsName}}</span>
                                    <span class="foodImg">￥{{food.price}}元</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                    </el-tabs>
                </div>

            </el-col>
        </el-row>

    </div>
</template>

<script>
import axios from 'axios';  //引入axios（跟ajax一样获取数据）
export default {
    name: 'pos',
    methods: {
        // handleClick() {
        //     console.log(1);
        // }
    },
    data() {
        return {
            tableData: [],
            oftenGoods: [],
            type0Goods: [],
            type1Goods: [],
            type2Goods: [],
            type3Goods: [],
            totalCount: 0,
            totalMoney: 0
        }
    },
    // 生命周期的函数
    created() {
        axios.get('http://jspang.com/DemoApi/oftenGoods.php')
            .then(req => {         //成功的回调函数
                // console.log(req);
                this.oftenGoods = req.data;     //把获取到数据，赋值给oftenGoods这个哦我们在上面定义的数组
            })
            .catch(error => {      //失败的回调函数
                console.log(error);
            })

        axios.get('http://jspang.com/DemoApi/typeGoods.php')
            .then(req => {
                console.log(req);
                this.type0Goods = req.data[0];
                this.type1Goods = req.data[1];
                this.type2Goods = req.data[2];
                this.type3Goods = req.data[3];
            })
            .catch(error => {
                console.log(error);
            })

    },
    mounted() { //mounted   当vue实例把视图嵌入完毕后执行

        //在页面中使用了Element组件，这样他会自动给我们生产虚拟DOM，我们无法设置高度100%；这时候可以利用javascript，来设置100%高度问题。先要给我们的<el-col>标签上添加一个id，我们这里把ID设置为
        var ordersHeight = document.body.clientHeight;
        console.log(ordersHeight);
        document.getElementById('orders').style.height = ordersHeight + 'px';
    },
    // 这里存放所有的函数，用于业务逻辑处理
    methods: {

        addOrderList(goods) {
            // 点击的时候，先把金额跟总数量清零
            this.totalCount = 0;
            this.totalMoney = 0;
            //商品是否已经存在订单列表中
            let isHave = false;
            for (let i = 0; i < this.tableData.length; i++) {
                // console.log(this.tableData[i].goodsId)
                if (this.tableData[i].goodsId == goods.goodsId) {
                    // this.count++;
                    isHave = true;
                }
            }
            //
            if (isHave) {
                // let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
                let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
                arr[0].count++;
            } else {
                let NewGoods = { goodsId: goods.goodsId, goodsImg: goods.goodsImg, goodsName: goods.goodsName, price: goods.price, count: 1 };
                this.tableData.push(NewGoods);
            }
            this.getAllMoney();//调用

            // 汇总数量和金额----原本在这里的

        },

        // 单品单个删除功能
        delOrderList(goods) {
            this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);//这里的过滤，系点击的id不等于里面的id，就赋值给左边的数组
            this.getAllMoney();//调用
        },
        // 全部商品删除
        delAllGoods() {
            this.tableData = [];
            this.tabscount = 0;
            this.tabsMoney = 0;
        },
        // 模拟结账--真正的结账，是要传值给后台的
        checkout() {
            // 当数量不等于0，就进入去，
            if (this.totalCount != 0) {
                this.tableData = [];
                this.totalCount = 0;
                this.totalMoney = 0;
                // this.$message({
                //     message:'结账成功',
                //     type:'success'
                // })
                this.$message.success('结账成功')
            }else{
                this.$message.error('不能为空');
            }
        },
        // 汇总数量和金额
        getAllMoney() {
            this.totalCount = 0; //汇总数量清0
            this.totalMoney = 0;
            if (this.tableData) {
                // 计算汇总金额和数量
                this.tableData.forEach((element) => {
                    this.totalCount += element.count;
                    this.totalMoney = this.totalMoney + (element.price * element.count);
                });
            }
        }


    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--scoped  定义了这个，那么接下来写的样式，全部都系定义在这个页面，如果不写，就系等于全局-->
<style scoped>
/* .pos {
    background: pink;
} */

.order {
    background: #f9fafc;
    border-right: 1px solid #c0ccda;
}

.block {
    padding-top: 10px;
}

.title {
    height: 20px;
    border-bottom: 1px solid #d3dce6;
    background-color: #f9fafc;
    padding: 10px;
    text-align: left;
}

.often-goods-list ul li {
    list-style: none;
    float: left;
    border: 1px solid #e5e9f2;
    padding: 10px;
    margin: 10px;
    background: #fff;
    cursor: pointer;
}

.money {
    color: #58b7ef;
}














/* 清除浮动 */

.goods-type {
    clear: both;
}

.cookList li {
    list-style: none;
    width: 23%;
    border: 1px solid #E5E9F2;
    height: auot;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
    cursor: pointer;
}

.cookList li span {

    display: block;
    float: left;
}

.foodImg {
    width: 40%;
}

.foodName {
    font-size: 18px;
    padding-left: 10px;
    color: brown;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
}

.TabsDiv {
    background-color: #fff;
    padding: 10px;
    border-bottom: 1px solid #ddd;
}
</style>



