// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import ElementUi from 'element-ui'    //引入ElementUi组件（已经下载左了，因为用的是npm下载，所以直接引入就可以了）
import 'element-ui/lib/theme-default/index.css'   //引入ElementUi组件的CSS---ElementUi文档里面的快速上手有


Vue.use(ElementUi);     //这样use一下，就是把这个引入的组件，变成全局的，这样全局就可以用了

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



